.footer {
  &__container {
    display: flex;
    padding-bottom: 109px;

    @include high-tablet {
      padding-bottom: 60px;
    }
  }

  &__rights {
    margin: 0;
    margin-right: 68px;
    font-family: 'Open Sans';
    font-weight: 400;
    font-size: 15px;
    line-height: 20px;
    color: var(--dark-color);
  }

  &__author {
    margin: 0;
    font-family: 'Open Sans';
    font-weight: 400;
    font-size: 15px;
    line-height: 20px;
    color: var(--dark-color);
  }
}
