.about {

  margin: 0;

  &__title {
    margin: 0;
    margin-bottom: 58px;
    font-family: 'Anton';
    font-weight: 400;
    font-size: 50px;
    line-height: 75px;
    text-transform: uppercase;
    color: var(--dark-color);
  }

  &__texts {
    display: flex;
    margin-bottom: 77px;

    @include small-tablet {
      flex-direction: column;
    }
  }

  &__text {
    max-width: 50%;
    color: var(--dark-color);

    @include small-tablet {
      max-width: 100%;
    }
  }

  &__text:first-child {
    margin-right: 20px;

    @include small-tablet {
      margin-right: 0;
      margin-bottom: 40px;
    }
  }

  &__text:last-child {
    padding-left: 15px;

    @include small-tablet {
      padding-left: 0;;
    }
  }

  &__header {
    margin: 0;
    margin-bottom: 30px;
    font-family: 'Anton';
    font-weight: 400;
    font-size: 25px;
    line-height: 38px;
    text-transform: uppercase;
  }

  &__descr {
    margin: 0;
    font-family: 'Open Sans';
    font-weight: 400;
    font-size: 16px;
    line-height: 26px;
    text-align: justify;
  }

  &__images {
    display: flex;
    justify-content: space-between;

    picture {
      margin-right: 5px;
    }

    picture:last-child {
      margin-right: 0;
    }

    @include small-tablet {
      justify-content: center;
      flex-wrap: wrap;

      picture {
        margin-bottom: 10px;
      }
    }
  }
}
