.header {

  &__container-fluid {
    background-image: url('../img/header-bg.png');
    background-size: cover;
    background-position: center 0;
    background-repeat: no-repeat;
  }

  &__container {
    padding-top: 56px;
    font-family: 'Anton';
    color: var(--light-color);

    @include small-tablet {
      padding-top: 30px;
    }
  }

  &__top {
    margin-bottom: 214px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__left {
    display: flex;
  }

  &__logo {
    margin-right: 70px;

    @include small-tablet {
      margin-right: 40px;
    }
  }

  &__phone {
    letter-spacing: 1.9px;
    color: var(--light-color);

    @include small-tablet {
      position: relative;
      margin-right: 10px;
      margin-left: auto;
      border: 1px solid rgba(255, 255, 255, 0.4);
      border-radius: 10px;
      width: 44px;
      height: 44px;
      font-size: 0;
      background-image: url("../img/phone.svg");
      background-position: center;
      background-size: 24px 24px;
      background-repeat: no-repeat;
    }
  }

  &__text {
    padding-bottom: 322px;
    font-family: 'Anton';
    font-weight: 400;
    color: var(--light-color);
  }

  &__descr {
    margin: 0;
    margin-bottom: 7px;
    font-size: 30px;
    line-height: 45px;
    letter-spacing: 0.1em;
    text-transform: uppercase;
  }

  &__title {
    margin: 0;
    margin-bottom: 38px;
    font-size: 96px;
    text-transform: uppercase;
    letter-spacing: 1.7px;

    @include mobile {
      font-size: 74px;
    }
  }

  &__btn {
    width: 238px;
    height: 57px;
    border: 1px solid #FFFFFF;
    font-size: 17px;
    line-height: 26px;
    text-align: center;
    letter-spacing: 1.6px;
    text-transform: uppercase;
    color: var(--light-color);
  }
}


