.nav {
    display: flex;
    align-items: center;

    &__list {
      display: flex;

      @include small-tablet {
        @include flex-all-center;

        flex-direction: column;
        width: 100%;
        height: 100%;
      }
    }

    &__item {
      margin-right: 40px;
      font-weight: 400;
      font-size: 14px;
      letter-spacing: 1.35px;
      line-height: 21px;
      text-transform: uppercase;
      color: var(--light-color);

      &:last-child {
        margin-right: 0;
      }

      @include small-tablet {
        margin-right: 0;
        margin-bottom: 30px;
        line-height: inherit;
        font-size: 32px;
      }
    }

    &__link {
      color: inherit;
    }

    @include small-tablet {
      position: fixed;
      left: 0;
      top: 0;
      z-index: 500;
      overflow-y: auto;
      width: 100%;
      height: 100vh;
      background-color: var(--bg-color);
      opacity: 0;
      visibility: hidden;
      transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
    }
  }

  .menu--active {
    opacity: 1;
    visibility: visible;
  }
